<template>
  <div id="chat">
    <chathead :username="$store.state.nowchat"></chathead>
    <div id="chatcontent">
        <ul v-for="item in $store.state.chatmes">
          <div class="left_item" v-if="item.from==$store.state.nowchat">
            <div class="title">
              <p>{{item.from}}</p>
              <p class="time">{{item.time}}</p>
            </div>
            <p class="mesfont">{{item.mes}}</p>
          </div>
          <div class="right_item" v-if="item.from!=$store.state.nowchat">
            <div class="title">
              <p>{{item.from}}</p>
              <p class="time">{{item.time}}</p>
            </div>
            <p class="mesfont">{{item.mes}}</p>
          </div>
        </ul>
    </div>
    <chatinput :type="type"></chatinput>
  </div>
</template>

<script>
import chatinput from '../common/chat_input';
import chathead from '../common/chat_head';
export default {
  name: "chat",
  components:{
    chathead,
    chatinput
  },
  data() {
    return {
      type:'group',
      myid: "",
    };
  },
  mounted() {
    let data = { username: localStorage.username };
    this.$socket.emit("getid", data);
  },
  sockets: {
    chatmes: function(data) {
      if (data.from != this.$store.state.nowchat) {
        this.$store.commit("pushchatmes", {
          from: data.from,
          to: data.to,
          mes: data.mes,
          time:data.time,
        });
      }
      this.$store.commit("pushchatlist", data.from);
    }
  },
};
</script>

<style lang="less" scoped>
li {
  list-style: none;
}
#chat {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}
 #menu{
    z-index: 1000;
    width: 30px;
    height: 30px;
    position: absolute;
    top:1%;
    left: 1%;
  }
#chatlist{
  position: absolute;
  height: 50%;
  width: 100%
}
#chatcontent {
  width: 90%;
  margin-left: 5%;
  height: 75%;
  overflow-y: scroll;
  .left_item{
    margin-top: 10px;
  }
  .right_item {
    margin-top: 10px;
    text-align: right;
  }
  .mesfont{
    font-size: 20px;
  }
  .title{
    p{
      display: inline-block;
      font-weight: bolder;
      margin-bottom: 5px;
    }
    .time{
      color: rgb(175, 175, 175);
      font-weight: lighter;
      font-size: 10px;
    }
  }
}
</style>



