<template>
  <div id="group_chat">
    <chathead :username="$store.state.nowgroup"></chathead>
    <div id="chatcontent">
        <ul v-for="item in $store.state.groupmes">
          <div class="left_item" v-if="item.from!=myname">
            <div class="title">
              <p>{{item.from}}</p>
              <p class="time">{{item.time}}</p>
            </div>
            <p class="mesfont">{{item.mes}}</p>
          </div>
          <div class="right_item" v-if="item.from==myname">
            <div class="title">
              <p>{{item.from}}</p>
              <p class="time">{{item.time}}</p>
            </div>
            <p class="mesfont">{{item.mes}}</p>
          </div>
        </ul>
    </div>
    <chatinput :type="type"></chatinput>
  </div>
</template>

<script>
import grouplist from "./grouplist";
import chathead from "../common/chat_head";
import chatinput from "../common/chat_input";
export default {
  components: {
    grouplist,
    chathead,
    chatinput
  },
  name: "group_chat",
  data() {
    return {
      type:'user',
      myname: localStorage.username,
      myid: "",
    };
  },
  sockets: {
    groupmes: function(data) {
      if (
        data.to == this.$store.state.nowgroup &&
        data.from != localStorage.username
      ) {
        this.$store.commit("pushgroupchat", {
          from: data.from,
          to: data.to,
          mes: data.mes,
          time: data.time
        });
      }
    }
  }
};
</script>

<style lang="less" scoped>
li {
  list-style: none;
}
#group_chat {
  height: 100%;
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
}
#grouplist {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height: 40%;
}
#menu {
  z-index: 1000;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 1%;
  left: 1%;
}
#chatcontent {
  width: 90%;
  margin-left: 5%;
  height: 75%;
  overflow: scroll;
  .left_item {
    margin-top: 10px;
  }
  .right_item {
    margin-top: 10px;
    text-align: right;
  }
  .mesfont {
    font-size: 20px;
  }
  .title {
    p {
      display: inline-block;
      font-weight: bolder;
      margin-bottom: 5px;
    }
    .time {
      color: rgb(175, 175, 175);
      font-weight: lighter;
      font-size: 10px;
    }
  }
}
</style>



